.App {
  text-align: center;
  background-color: #ebedf0;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  padding: 1vh;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 5vh;
}

.Container {
  background-color: white;
}

.Box {
  text-align: left;
}

.Header {
  margin-left: 10vh;
  text-align: left;
}

.App-link {
  color: #61dafb;
}

.Label {
  margin-bottom: 5vh;
  margin-top: 5vh;
}

.Radio {
  text-align: left;
}
.FormControlLabel {
  margin-bottom: 2vh;
}

.Button {
  padding-bottom: 10vh;
}
